import React from "react";

import { graphql, useStaticQuery } from "gatsby";
import { Helmet } from 'react-helmet';
import Img from 'gatsby-image';

import Layout from "../components/layout_inner";
import SocialLinks from "../components/social_links";

export default (props) => {
  const data = useStaticQuery(graphql`
    query {
      main: file(relativePath: { eq: "main.jpg" }) {
        childImageSharp {
          fluid(quality: 100, maxWidth: 1920) {
            ...GatsbyImageSharpFluid_withWebp_tracedSVG
          }
        }
      }
      me: file(relativePath: { eq: "me.jpg" }) {
        childImageSharp {
          fluid(quality: 100, maxWidth: 1920) {
            ...GatsbyImageSharpFluid_withWebp_tracedSVG
          }
        }
      }
    }
  `);

  return (
    <Layout {...props}>
      <Helmet title="Фотограф Минск | Ольга Емельянцева" defer={false} />
      <div className="main-page-header">
        <h1>Фотограф Минск</h1>
        <Img fluid={data.main.childImageSharp.fluid} alt="Фото из портфолио" />
      </div>
      <div className="main-page-container">
        <div className="main-page-content">
          <h2>
            <b>Привет!</b><br />
            Меня зовут Ольга Емельянцева и я фотограф.
          </h2>
          <Img fluid={data.me.childImageSharp.fluid} className="desktop-hidden" alt="Ольга Емельянцева" />
          <p>
            Снимаю в городе Минске и Минском районе. Выезжаю в другие города Беларуси.<br />
            Минимальный пакет для выездной съемки - 8 часов, к стоимости пакета добавляются расходы на топливо.
          </p>
          <p>
            Виды фотосъёмок, на которых я специализируюсь:<br />
            Индивидуальная фотосессия, Студийная съёмка, Контент-съемка, Фотосессия Love Story, Детская фотосессия / Семейная фотосессия, Фотосессия в ожидании чуда, Свадебная фотосъёмка (прогулка).<br />
            Всегда на фотосъёмке стараюсь выложиться на все 100%. К работе подхожу ответственно, чтоб добиться желаемого результата. 
            Помогу подготовиться и подскажу как, где, когда и почему.
            Также помогу найти нужных визажистов и парикмахеров.
          </p>
          <p>
            Главная моя задача - сделать фотосъемку комфортной для Вас, раскрепостить и показать Ваши сильные стороны.
          </p>
          <p>
            С нетерпением жду работы с Вами в ближайшее время ;) !
          </p>
          <h2 className="smaller">
            Записаться на фотосессию
          </h2>
          <SocialLinks />
        </div>
        <Img fluid={data.me.childImageSharp.fluid} className="tablet-hidden" alt="Ольга Емельянцева" />
      </div>
    </Layout>
  );
};
